import Vue from 'vue'
import Vuex from 'vuex'
import { getGoods } from "../api/api";

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    data : [1]
  },
  mutations: {
    getGoodsInfo(state,n){
      state.data = n;
    },
    add(state,{n,name}){
      let data = state.data;
      for(let i in data){
        for(let j in data[i].foods){
          if(data[i].foods[j].name == name){
            data[i].foods[j].num += n;
            break;
          }
        }
      }
    }
  },
  actions: {
    async getGoodsInfo(context) {
      let d = await getGoods();
      for(let i in d){
        let food = d[i].foods;
        for(let j in food){
          food[j].num = 0;
        }
      }
      context.commit('getGoodsInfo',d)
    }
  },
  getters : {
    showCar(state){
      let arr = [];
      let data = state.data;
      for(let i in data){
        for(let j in data[i].foods){
          if(data[i].foods[j].num > 0){
            let flag = true;
            for(let k in arr){
              if(arr[k].name == data[i].foods[j].name){
                flag = false;
                break;
              }
            }
            if(flag){
              arr.push(data[i].foods[j]);
            }
          }
        }
      }
      return arr;
    }
  },
  modules: {
  }
})