import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue';

Vue.use(VueRouter)
export default new VueRouter({
  routes : [
    {path: '/',component: Home,
    redirect:'/Home/Goods',
    children:[
      {path:'/Home/Goods',component:() => import('../views/Goods.vue')},
      {path:'/Home/Evaluation',component:() => import('../views/Evaluation.vue')},
      {path:'/Home/Merchants',component:() => import('../views/Merchants.vue')}
    ]
    },
    {path: '/goods_details',component: () => import('../views/Goods_details.vue')}
  ]
})
