<template>
  <div id="home">
    <header>
      <div class="left" style="width:80px;height:80px;">
        <img :src="data.avatar" style="width:100%;height:100%;border-radius:4px" />
      </div>
      <div class="right" style="width:calc(100% - 80px)">
        <p>
          <van-tag type="danger" style="font-size:14px;font-weight:bold">品牌</van-tag>
          <span
            style="vertical-align:middle;margin-left:4px;font-size:16px;color:#fff;font-weight:bold"
          >{{data.name}}</span>
        </p>
        <p>
          <span>{{data.description}} /</span>
          <span>{{data.deliveryTime}}</span>分钟送达
        </p>
        <p style="line-height:25px;display:flex;justify-content:space-between;font-size:14px">
          <span>
            <van-tag
              color="#fff"
              text-color="red"
              style="font-weight:bold;font-size:14px;padding:0px 2px;margin-right:4px"
            >减</van-tag>
            {{data.supports[0].description}}
          </span>
          <span
            style="width:50px;height:25px;text-align:center;border-radius:30px;background-color:rgb(68, 68, 68)"
            @click="show=true"
          >{{(data.supports || []).length}}个></span>
          <van-overlay :show="show" class="actives">
            <div style="line-height:50px">
              <p style="font-size:20px;font-weight:400">{{data.name}}</p>
              <van-rate
                v-model="data.foodScore"
                :size="25"
                color="#fe9900"
                gutter="15px"
                void-color="#61666c"
                void-icon="star"
                allow-half
                readonly
              />
            </div>
            <div style="display:flex;justify-content:space-between;align-items:center">
              <span style="flex:1;border-bottom:2px solid #61666c"></span>
              <span style="flex:1;font-size:16px">优惠信息</span>
              <span style="flex:1;border-bottom:2px solid #61666c"></span>
            </div>
            <div style="box-sizing:border-box;padding:10px 15px;text-align:left;">
              <p
                v-for="(v,i) in data.supports"
                :key="i"
                style="color:#c9cdcc"
              >{{data.supports[i].description}}</p>
            </div>
            <div style="display:flex;justify-content:space-between;align-items:center">
              <span style="flex:1;border-bottom:2px solid #61666c"></span>
              <span style="flex:1;font-size:16px">商家公告</span>
              <span style="flex:1;border-bottom:2px solid #61666c"></span>
            </div>
            <div style="box-sizing:border-box;padding:10px 15px;text-align:left">
              <p style="color:#c9cdcc">{{data.bulletin}}</p>
            </div>
            <div
              style="position:absolute;bottom:60px;left:50%;margin-left:-13px;font-size:26px;color:#90979a"
              @click="show = false"
            >
              <van-icon name="cross" />
            </div>
          </van-overlay>
        </p>
      </div>
    </header>
    <div class="bar">
      <van-tag
        color="#fff"
        text-color="#333"
        style="width:28px;height:16px;padding:0;margin-top:7px;padding-left:2px"
      >公告</van-tag>
      <van-notice-bar style="flex:1;padding-left:6px" mode="link">{{data.bulletin}}</van-notice-bar>
    </div>
    <van-sticky>
      <router-link to="/Home/Goods" :class="$route.path == '/Home/Goods' ? 'active' : ''">商品</router-link>
      <router-link
        to="/Home/Evaluation"
        :class="$route.path == '/Home/Evaluation' ? 'active' : ''"
      >评价</router-link>
      <router-link to="/Home/Merchants" :class="$route.path == '/Home/Merchants' ? 'active' : ''">商家</router-link>
    </van-sticky>
    <div @click="showcar = false">
      <router-view></router-view>
    </div>
    <footer>
      <div class="showcar" v-show="showcar">
        <p v-for="(v,i) in foodArr" :key="i">
          <span style="flex:3">{{v.name}}</span>
          <span style="flex:3;color:red">￥{{v.price}}</span>
          <span class="btn" v-show="v.num" @click="$store.commit('add',{n:-1,name:v.name})">-</span>
          {{v.num > 0 ? v.num : ''}}
          <span
            class="btn"
            style="background-color:#1989fa;color:#fff;margin-right:0;margin-left:10px"
            @click="$store.commit('add',{n:1,name:v.name})"
          >+</span>
        </p>
      </div>
      <div style="width:110px;display:flex;justify-content:flex-end">
        <div
          @click="car()"
          style="width:60px;height:60px;position:absolute;left:10px;top:-10px;border-radius:50%;background-color:rgb(76, 76, 76);box-sizing:border-box;padding:7px;z-index:10"
        >
          <div class="car" :class="`${foodArr.length > 0 ? 'foods' : ''}`">
            <van-icon name="shopping-cart" />
          </div>
        </div>
        <span
          :style="`width:50%;text-align:right;${allPrice > 0 ? 'color:rgb(246, 211, 38)' : ''}`"
        >￥{{allPrice}}</span>
      </div>
      <div style="flex:1;text-align:left;padding-left:15px;">
        |
        <span
          style="font-size:16px;padding-left:5px;font-weight:300;vertical-align:top"
        >另需配送费￥{{data.deliveryPrice}}元</span>
      </div>
      <div style="width:110px;background-color:rgb(90,90,90)">￥{{data.minPrice}}起送</div>
    </footer>
  </div>
</template>

<script>
import { getInfo } from "../api/api";
export default {
  data() {
    return {
      data: {},
      show: false,
      showcar: false,
      value: 2.5,
      num: 0
    };
  },
  methods: {
    async info() {
      let d = await getInfo();
      this.data = d;
    },
    car() {
      this.showcar = !this.showcar;
    },
    add(n) {
      this.num += n;
    }
  },
  computed: {
    foodArr() {
      let foods = this.$store.getters.showCar;
      return foods;
    },
    allPrice() {
      let foods = this.$store.getters.showCar;
      let all = 0;
      for (let i in foods) {
        all += foods[i].num * foods[i].price;
      }
      return all;
    }
  },
  created() {
    this.info();
  }
};
</script>
<style>
#home > header {
  box-sizing: border-box;
  padding: 30px;
  display: flex;
  background-color: rgb(99, 95, 95);
  justify-content: space-around;
}
#home header p {
  color: #fff;
  font-size: 16px;
  font-weight: 300;
}
#home .right {
  margin-left: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
#home .van-sticky {
  display: flex;
  border-bottom: 1px solid #ccc;
  background: #fff;
}
#home .van-sticky > a {
  flex: 1;
  text-align: center;
  height: 50px;
  line-height: 50px;
  color: rgb(85, 84, 84);
  font-size: 18px;
}
#home .van-sticky > a.active {
  color: red;
}
#home .van-notice-bar {
  height: 30px;
  background-color: rgb(88, 88, 88);
  color: #fff;
  font-size: 12px;
  font-weight: 300;
}
#home .bar {
  display: flex;
  height: 30px;
  line-height: 30px;
  justify-content: space-between;
  background-color: rgb(88, 88, 88);
  padding-left: 16px;
}
#home footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  line-height: 60px;
  background-color: rgb(76, 76, 76);
  display: flex;
  text-align: center;
  font-size: 20px;
  color: rgb(177, 176, 176);
  z-index: 3;
}
#home .showcar {
  position: absolute;
  width: 100%;
  height: 160px;
  background: rgb(240, 248, 255);
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  bottom: 60px;
  left: 0;
  overflow: scroll;
  z-index: 2;
  box-sizing: border-box;
  padding: 20px;
  border-top: 2px solid #ccc;
}
#home .showcar > p {
  display: flex;
  justify-content: space-between;
  align-items: center;
  line-height: 50px;
  text-align: center;
  border-bottom: 1px solid #ccc;
}
#home .car {
  width: 100%;
  height: 100%;
  line-height: 54px;
  text-align: center;
  background-color: rgb(107, 107, 107);
  border-radius: 50%;
}
#home .foods > i {
  color: rgb(246, 211, 38) !important;
}
#home .car .van-icon {
  font-size: 28px;
  color: rgb(150, 150, 150);
}
#home .actives {
  box-sizing: border-box;
  padding: 120px 40px;
  z-index: 10;
  text-align: center;
}
#home .actives > div {
  margin-bottom: 10px;
}
#home .showcar .btn {
  display: inline-block;
  height: 20px;
  width: 20px;
  text-align: center;
  border-radius: 50%;
  font-size: 24px;
  line-height: 18px;
  border: 2px solid #1989fa;
  color: #1989fa;
  vertical-align: middle;
  margin-right: 10px;
}
</style>
